




















import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { Data, ColorSets, Foregrounds, Backgrounds } from './Meta';
import TSampler from './TSampler.vue';
import CSampler from './CSampler.vue';

@Component({
  components: {
    TSampler,
    CSampler
  }
})
export default class Canvas extends Vue {
  canvas: HTMLCanvasElement|null = null
  context: CanvasRenderingContext2D|null = null

  featerWidth = 1024;
  featerHeight = 500;

  @Prop({ default: new Data() })
  data!: Data;
  @Watch("data", { deep: true })
  onDataChange() {
    this.redraw();
  }

  aligns = ["left", "center", "right"];

  mounted() {
    this.canvas = document.getElementById('canvas') as HTMLCanvasElement;
    this.canvas.width = this.featerWidth;
    this.canvas.height = this.featerHeight;
    this.context = this.canvas.getContext('2d');

    this.redraw();
  }

  redraw() {
    const colorSet = this.currentColorSet();
    const template = this.currentTemplate();

    if (this.context == null || colorSet == null || template == null) return;

    this.context.textBaseline = "middle";

    if (this.data.backgroundKey == 0) {
      this.context.fillStyle = colorSet.background1;
      this.context.fillRect(0, 0, this.featerWidth, this.featerHeight);
    } else if (this.data.backgroundKey == 1) {
      const grad = this.context.createLinearGradient(0, this.featerHeight / 2, this.featerWidth, this.featerHeight / 2);
      grad.addColorStop(0, colorSet.background1);
      grad.addColorStop(1, colorSet.background2);
      this.context.fillStyle = grad;
      this.context.fillRect(0, 0, this.featerWidth, this.featerHeight);
    } else if (this.data.backgroundKey == 2) {
      const grad = this.context.createLinearGradient(this.featerWidth / 2, 0, this.featerWidth / 2, this.featerHeight);
      grad.addColorStop(0, colorSet.background1);
      grad.addColorStop(1, colorSet.background2);
      this.context.fillStyle = grad;
      this.context.fillRect(0, 0, this.featerWidth, this.featerHeight);
    } else if (this.data.backgroundKey == 3) {
      const grad = this.context.createRadialGradient(this.featerWidth / 2, this.featerHeight / 2, 50, this.featerWidth / 2, this.featerHeight / 2, this.featerHeight);
      grad.addColorStop(0, colorSet.background1);
      grad.addColorStop(1, colorSet.background2);
      this.context.fillStyle = grad;
      this.context.fillRect(0, 0, this.featerWidth, this.featerHeight);
      this.context.fill();
    } else if (this.data.backgroundKey == 4) {
      this.context.fillStyle = colorSet.background1;
      this.context.fillRect(0, 0, this.featerWidth, this.featerHeight);
      this.context.fillStyle = colorSet.background2;
      this.context.beginPath();
      this.context.moveTo(this.featerWidth / 2 + 100, 0);
      this.context.lineTo(this.featerWidth, 0);
      this.context.lineTo(this.featerWidth, this.featerHeight);
      this.context.lineTo(this.featerWidth / 2 - 100, this.featerHeight);
      this.context.closePath();
      this.context.fill();
    } else if (this.data.backgroundKey == 5) {
      this.context.fillStyle = colorSet.background1;
      this.context.fillRect(0, 0, this.featerWidth, this.featerHeight);
      this.context.fillStyle = colorSet.background2;
      this.context.beginPath();
      this.context.moveTo(0, this.featerHeight / 2 + 100);
      this.context.lineTo(0, this.featerHeight);
      this.context.lineTo(this.featerWidth, this.featerHeight);
      this.context.lineTo(this.featerWidth, this.featerHeight / 2 - 100);
      this.context.closePath();
      this.context.fill();
    } else if (this.data.backgroundKey == 6) {
      this.context.fillStyle = colorSet.background1;
      this.context.fillRect(0, 0, this.featerWidth, this.featerHeight);
      this.context.fillStyle = colorSet.background2;
      this.context.beginPath();
      this.context.moveTo(this.featerWidth, 0);
      this.context.bezierCurveTo( this.featerWidth / 2, 0, this.featerWidth / 2, this.featerHeight, 0, this.featerHeight );
      this.context.lineTo(this.featerWidth, this.featerHeight);
      this.context.closePath();
      this.context.fill();
    }

    this.context.fillStyle = colorSet.title;
    this.context.font = `${template.titleSize}pt ${this.data.font}`;
    this.context.textAlign = template.titleAlign as CanvasTextAlign;
    this.context.fillText(this.data.title, template.titleLeft, template.titleTop);

    this.context.fillStyle = colorSet.description;
    this.context.font = `${template.descriptionSize}pt ${this.data.font}`;
    this.context.textAlign = template.descriptionAlign as CanvasTextAlign;
    this.context.fillText(this.data.description, template.descriptionLeft, template.descriptionTop);

    let ss1Path = "";
    if (this.data.ss1Path != null) ss1Path = this.data.ss1Path;
    else ss1Path = require("@/assets/ss.png");
    const ss1Img = new Image();
    ss1Img.src = ss1Path;
    ss1Img.onload = this.ss1OnLoad;

    let ss2Path = "";
    if (this.data.ss2Path != null) ss2Path = this.data.ss2Path;
    else ss2Path = require("@/assets/ss.png");
    const ss2Img = new Image();
    ss2Img.src = ss2Path;
    ss2Img.onload = this.ss2OnLoad;

    let iconPath = "";
    if (this.data.iconPath != null) iconPath = this.data.iconPath;
    else iconPath = require("@/assets/icon.png");
    const iconImg = new Image();
    iconImg.src = iconPath;
    iconImg.onload = this.iconOnLoad;
  }
  
  ssBaseWidth = 250;
  ssBaseHeight = 450;
  iconBaseSize = 125;
  _ss1OnLoad(e: any) {
    const template = this.currentTemplate();
    if (this.context == null || template == null) return;

    const w = this.ssBaseWidth * template.ss1Scale;
    const h = this.ssBaseHeight * template.ss1Scale;
    this.context.drawImage(e.target, template.ss1Left - w/2, template.ss1Top - h/2, w, h);
  }
  ss1OnLoad = this._ss1OnLoad.bind(this);
  _ss2OnLoad(e: any) {
    const template = this.currentTemplate();
    if (this.context == null || template == null) return;

    const w = this.ssBaseWidth * template.ss2Scale;
    const h = this.ssBaseHeight * template.ss2Scale;
    this.context.drawImage(e.target, template.ss2Left - w/2, template.ss2Top - h/2, w, h);
  }
  ss2OnLoad = this._ss2OnLoad.bind(this);
  _iconOnLoad(e: any) {
    const template = this.currentTemplate();
    if (this.context == null || template == null) return;

    const w = this.iconBaseSize * template.iconScale;
    const h = this.iconBaseSize * template.iconScale;
    this.context.drawImage(e.target, template.iconLeft - w/2, template.iconTop - h/2, w, h);
  }
  iconOnLoad = this._iconOnLoad.bind(this);

  download() {
    const a = document.getElementById("downloadLink") as HTMLAnchorElement;
    a.href = this.canvas?.toDataURL() as string;
    a.click();
  }

  tSampler: any;
  private currentTemplate(): any {
    // return this.tSampler;
    return Foregrounds.find(it => it.key == this.data.foregroundKey);
  }
  changeTSampler(s: any) {
    // this.tSampler = s;
    this.redraw();
  }

  cSampler: any;
  private currentColorSet(): any {
    // return this.cSampler;
    return ColorSets.find(it => it.key == this.data.colorSetKey);
  }
  changeCSampler(s: any) {
    // this.cSampler = s;
    this.redraw();
  }
}
